/*Propiedades del contenedor de la información*/
.info-container{
    width: 100%;
    height: 300px;
    background-color: white;
    color: black;
    margin-bottom: 100vh;
}

/*Propiedades de la información.*/
.Info{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

/*Agregando parte responsive*/
@media only screen and (max-width: 960px){
    .info-container{
        height: 200px;
        width: 100%;
    }

    .info > h1 {
        text-align: center;
        font-size: 25px;
    }
}