/*Propiedades generales del footer*/
.footer{
    width: 100%;
    height: 100vh;
    background-color: black;
    color: white;
    padding-top: 180px;
    position: fixed;
    z-index: -2;
    bottom: 0;
    left: 0;
}

/*Propiedades de la info del footer*/
.footer-info{
    font-size: 35px;
    padding: 30px 0 30px 50px;
    width: 70%;
    margin: 0 auto;
}

/*Propiedades del título*/
.footer-info > h1 {
    padding-bottom: 15px;
}

/*Propiedades de la info de contacto*/
.footer-contact{
    font-size: 22px;
    padding: 100px 0 30px 50px;
    width: 70%;
    margin: 0 auto, 
}

/*Propiedades del sns*/
.footer-sns{
    width: 70%;
    position: absolute;
    bottom: 40px;
    left: 0;
    right: 0;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
}

/*Modificando los íconos*/
.sns-links{
    display: grid;
    width: 100%;
    grid-template-columns: 1fr 1fr 1fr;
}

/*Cambiando el diseño de los íconos*/
.twitter, 
.instagram, 
.facebook{
    color: white;
    font-size: 35px;
    margin: 0 auto;
    padding-bottom: 10px;
    border-bottom: 4px solid transparent;
}


/*agregando efecto hover a los íconos*/
.twitter:hover, 
.instagram:hover, 
.facebook:hover{
    border-color: white;
    transition: all 0.3s ease-out;
}

.design-by{
    font-size: 10px;
    padding-left: 50px;
    color: white;
}

/*Agregando parte responsive*/
@media only screen  and (max-width: 960px){
    .footer-info{
        text-align: center;
        padding: 0;
        font-size: 20px;
    }

    .footer-contact{
        font-size: 16px;
        padding: 100px 0 30px 0;
        text-align: center;
    }

    .footer-sns{
        grid-template-columns: 1fr;
        text-align: center;
    }

    .design-by{
        padding: 0 0 40px 0;
    }
}